.links {
  flex-flow: column;
  margin-top: 7.5rem;
  font-size: 0.75rem;
  letter-spacing: 0.02em;
  display: flex;
}

.logo {
  font-size: clamp(1rem, 0.928rem + 0.36vw, 1.25rem);
  display: inline-block;
}

.nav-links {
  /* width: 4,5rem; */
  line-height: 2.3;
  text-decoration: none;
  transition-property: all;
  transition-duration: 0.25s;
  transition-timing-function: ease-in-out;
  /* color: gray; */
}


/* .nav-links.current {
  color:black;
} */
.nav-links:hover {
  color: var(--color--black-40);
  padding-left: 0.25rem;
}

.nav-links.w--current {
  opacity: 1;
  color: var(--color--black-40);
}

.nav-links.nav-links-dark,
.logo-dark {
  color: var(--color--white);
}

.nav-links.nav-links-dark:hover,
.logo-dark:hover,
.nav-links.nav-links-dark.w--current {
  color: var(--color--white-40);
}

.nav-content {
  position: fixed;
  top: 2.5rem;
  z-index: 100;
}

.flipped {
  /* transition-property: all;
  transition-duration: 0.25s;
  transition-timing-function: ease-in-out; */
}

/* .flipped:hover, flipped-dark:hover {
  transform: scaleX(-1);
  display: inline-block;
} */

.flipped-dark {
  transform: scaleX(-1);
  display: inline-block;
}
.hero_wrap,
.inner-store-hero {
  /* margin-top: 25svh; */
  margin-top: 11.25rem;
  margin-bottom: 10rem;
}

.work-wrap {
  margin-top: 10rem;
  background-color: var(--color--black-3);
  padding: 2.5rem;
}

.work-wrap.work-wrap-dark {
  background-color: var(--color--white-10);
}

.cv-item {
  margin-top: 10rem;
  margin-bottom: 3.75rem;
}

.txt-item {
  margin-top: 3.75rem;
  margin-bottom: 3.75rem;
}

.paragraph,
.paragraph1 {
  column-count: 2;
  column-gap: 1.25rem;
  font-size: clamp(1rem, 0.986rem + 0.07vw, 1.05rem);
  line-height: 1.6;
}
.paragraph1 {
  column-count: 1;
}

.paragraph1-s {
  font-size: clamp(0.8125rem, 0.7768rem + 0.1786vw, 0.9375rem);
}

.feat-items {
  margin-top: 3.75rem;
  margin-bottom: 3.75rem;
}

.name {
  margin-top: 7.5rem;
}

.subtxt {
  color: var(--color--black-40);
  margin-top: 2.5rem;
  font-size: clamp(1rem, 0.986rem + 0.07vw, 1.05rem);
  max-width: 55ch;
}

.subtxt.subtxt-dark {
  color: var(--color--white-40);
}

.img-works {
  aspect-ratio: 320 / 593;
  width: 100%;
}

.txt-xl {
  margin-top: 0;
  margin-bottom: 0;
  font-size: clamp(2.02rem, 1.598rem + 2.11vw, 3.5rem);
  line-height: 1;
  letter-spacing: -0.01em;
}

.txt-l {
  font-size: clamp(1.6rem, 1.426rem + 0.87vw, 2.3405rem);
  line-height: 1;
  letter-spacing: -0.01em;
}

.txt-l.name {
  display: inline-block;
}

.txt-m {
  margin-bottom: 0;
  font-size: clamp(1.42rem, 1.326rem + 0.47vw, 1.8205rem);
  line-height: 1;
  letter-spacing: -0.003em;
}

.txt-s {
  margin-bottom: 0;
  font-size: clamp(1.125rem, 1.0893rem + 0.1786vw, 1.25rem);
  line-height: 1;
  letter-spacing: -0.003em;
}

.txt-smaller {
  font-size: clamp(1rem, 0.986rem + 0.07vw, 1.05rem);
}

ul > li {
  list-style: none;
}

.list {
  margin-top: 3.75rem;
  margin-bottom: 0;
  padding-left: 0;
}

.list-item {
  margin-bottom: 2.5rem;
}

.links-tt-wrap {
  grid-column-gap: 0.5em;
  grid-row-gap: 0.5em;
  margin-top: 1.25rem;
  display: flex;
}

.headshot {
  aspect-ratio: 1;
  width: 100%;
}

.feat-list {
  margin-top: 2.5rem;
  margin-bottom: 0;
  padding-left: 0;
}

.contact-link {
  color: var(--color--black);
  font-size: clamp(1rem, 0.986rem + 0.07vw, 1.05rem);
  line-height: 1;
}

.contact-link.contact-link-dark {
  color: var(--color--white);
}

.contact-link.contact-link-dark:hover {
  color: var(--color--white-40);
}

.copyright {
  margin-right: 0.25rem;
  font-size: clamp(0.65rem, 0.622rem + 0.14vw, 0.75rem);
  line-height: 1;
}

.likst-flex {
  grid-column-gap: 1.25rem;
  grid-row-gap: 0rem;
  display: flex;
}

.copy-flex {
  display: flex;
}

.body-light {
  color: var(--color--black);
  font-weight: 500;
}

.body-light.is-dark {
  background-color: var(--color--black);
  color: var(--color--white);
  font-weight: 400;
}

.af-list-item {
  font-size: clamp(1rem, 0.986rem + 0.07vw, 1.05rem);
}

.p-link-tt {
  color: var(--color--black);
  font-size: clamp(1rem, 0.986rem + 0.07vw, 1.05rem);
}

.p-link-tt.p-link-tt-dark {
  color: var(--color--white);
}

.list_item-awards {
  text-wrap: pretty;
  margin-bottom: 1.25rem;
}

.hide {
  display: none;
}

.product-links {
  text-decoration: none;
  color: var(--color--black);
}
.product-links-dark {
  color: var(--color--white);
}

.titleprice {
  display: flex;
  /* justify-content: space-between; */
  flex-direction: column;
  margin: 0.5rem 0 0.5rem 0;
}

.titleprice > .subtxt {
  margin-top: 0;
}

.titleprice > h2,
p {
  margin: 0;
  line-height: 1.4;
}

button {
  border: 0;
  padding: 0;
  background-color: transparent;
  text-align: left;
}

.product-wrapper {
  position: relative;
  max-width: 100%;
  background-color: var(--color--black-3);
  box-sizing: border-box;
  border: 0 solid var(--color--black-3);
  transition-property: all;
  transition-duration: 0.25s;
  transition-timing-function: ease-in-out;
  cursor: pointer;
}

.product-wrapper:hover {
  border: 1.5rem solid var(--color--black-3);
}

.product-wrapper-dark {
  background-color: var(--color--white-10);
}

.product-wrapper:after {
  content: "";
  display: block;
  padding-top: calc(100%);
}

ul.w-list-unstyled.grid-3,
ul.w-list-unstyled.grid-2 {
  padding: 0;
  margin: 0;
}

.product-image {
  position: absolute;
  width: 100%;
  height: 100%;
}

.work-wrap {
  margin-top: 10rem;
  background-color: var(--color--black-3);
  padding: 2.5rem;
}

.img-responsive {
  width: 100%;
  height: auto;
}

.buttons {
  margin-top: 2.5rem;
  display: flex;
  flex-direction: column;
  grid-row-gap: 0.5rem;
}

.buttons > * {
  flex: 1;
}

.add-btn,
.buy-btn {
  text-align: center;
  font-size: clamp(1rem, 0.986rem + 0.07vw, 1.05rem);
  font-weight: 500;
  letter-spacing: 0.02em;
  padding: 1rem;
  cursor: pointer;
  text-decoration: none;
  border-radius: 999rem;
  transition-property: all;
  transition-duration: 0.25s;
  transition-timing-function: ease-in-out;
}

.add-btn {
  color: var(--color--black);
  border: 1px solid var(--color--black);
}
.add-btn:hover {
  border-color: var(--color--black-40);
}

.buy-btn {
  color: var(--color--white);
  background-color: var(--color--black);
}
.buy-btn:hover {
  background-color: var(--color--black-40);
}

.add-btn-dark {
  color: var(--color--white);
  border: 1px solid var(--color--white);
}
.add-btn-dark:hover {
  border-color: var(--color--white-40);
}

.buy-btn-dark {
  color: var(--color--black);
  background-color: var(--color--white);
}

.nav-links.nav-links-dark,
.logo-dark {
  color: var(--color--white);
}
/* SWIPER */
.product-gallery {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  gap: 1.25rem;
}

/* Vertical thumbnails – let Swiper handle wrapper layout */
.product-thumbs {
  grid-column: span 1;
  max-width: 100%;
  height: auto;
  overflow: hidden;
}
.product-thumbs img {
  width: 100%;
  height: auto;
  -o-object-fit: cover;
  object-fit: cover;
  display: block;
  aspect-ratio: 1/1;
}
.product-thumbs .swiper-slide {
  height: auto; /* Swiper will size them based on slidesPerView */
  opacity: 0.5;
  cursor: pointer;
}

.product-thumbs .swiper-slide-thumb-active {
  opacity: 1;
}

.product-main {
  grid-column: span 5;
  width: 100%;
  aspect-ratio: 6 / 7;
  padding: 2.5rem;
}

.product-main img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

/* Pagination (pick ONE style) */
.swiper-pagination-bullet {
  background: #000;
  opacity: 0.25;
}
.swiper-pagination-bullet-active {
  opacity: 1;
}

.nav-grid {
  grid-area: 1 / 1 / 2 / 2;
}

.hero-grid {
  grid-area: 1 / 4 / 2 / 11;
  align-self: center;
}

.tt-link-grid {
  grid-area: 1 / 1 / 2 / 13;
  justify-self: end;
}

.cv-grid {
  grid-area: 1 / 3 / 2 / 8;
}

.story-grid {
  grid-area: 1 / 7 / 2 / 13;
}

/* store */
.products-grid {
  grid-area: 1 / 3 / 2 / 12;
}

.productgallery-grid {
  grid-area: 1 / 3 / 2 / 9;
}
.productdecription-grid {
  grid-area: 1 / 9 / 2 / 12;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.productdecription-grid > *:not(:last-child) {
  margin: 0;
}

.feat-grid {
  grid-area: 1 / 3 / 2 / 7;
}

.faq-grid {
  display: grid;
  grid-area: 1 / 4 / 2 / 13;
  grid-column-gap: 1.25rem;
  grid-row-gap: 1.25rem;
  grid-template-rows: auto;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-auto-columns: 1fr;
}

.footercopy-grid {
  grid-area: 1 / 1 / 2 / 3;
  align-self: end;
}

.footerimg-grid {
  grid-area: 1 / 7 / 2 / 10;
}

.footerlinks-grid {
  grid-area: 1 / 10 / 2 / 13;
  place-self: end;
}

.faq-item {
  grid-column: span 3;
}

a {
  color: var(--color--black);
  transition: color 0.25s ease-in-out;
}
a:hover {
  color: var(--color--black-40);
}
a.p-link-tt.p-link-tt-dark:hover {
  color: var(--color--white-40);
}
.mode-switch {
  cursor: pointer;
}
img::selection {
  background: transparent;
}
h1,
h2,
h3 {
  font-weight: inherit;
}

.page_wrap {
  overflow: clip;
}

::selection {
  background-color: #94949480;
}

